import { redirect } from "next/dist/server/api-utils";
import React from "react";
import "./button.css";

interface ButtonProps {
  /**
   * 버튼 배경 색깔
   */
  backgroundColor?: string;

  /**
   * 버튼 테두리 라운드 설정
   */
  borderRadius?: string;

  /**
   * React className 설정
   */
  className?: string[];

  /**
   * 버튼 내부 내용 설정
   */
  label: string;
  /**
   * 클릭 핸들러
   */
  onClick?: () => void;

  /**
   * style 설정 
   */
  style?: style;

}

/**
 * Primary UI component for user interaction
 */
export const Button = ({
  backgroundColor,
  label,
  borderRadius,
  className,
  style,
  onClick,
}: ButtonProps) => (
  <button
    type="button"
    style={{
      backgroundColor,
      borderRadius,
      ...style
    }}
    className={`button_default ${className.join(" ")}`}
    onClick={onClick}
  >
    {label}
  </button>
);
/**
 * react/require-default-props
 */
Button.defaultProps = {
  backgroundColor: "white",
  borderRadius: "0%",
  className: [],
  style: { background: "red"},
  onClick: () => {},
};

export default Button;
